export default (function() {
  const getCachedFavoritedBeers = () => {
    try {
      const serializedFavorited = localStorage.getItem('FAVORITED_BEERS');
      console.log({ serializedFavorited });
      if (!serializedFavorited || serializedFavorited === 'undefined') {
        return [];
      }
      return JSON.parse(serializedFavorited);
    } catch (error) {
      console.error(error);
    }
  };

  const saveFavoritedBeers = (state) => {
    try {
      const serializedFavorited = JSON.stringify(state.favoritedBeerIds);
      console.log('save', { serializedFavorited });
      localStorage.setItem('FAVORITED_BEERS', serializedFavorited);
    } catch (error) {
      console.error(error);
    }
  };

  return {
    getCachedFavoritedBeers: getCachedFavoritedBeers,
    saveFavoritedBeers: saveFavoritedBeers
  };
})();
