import React, { Component } from 'react';
import AppView from '../components/AppView';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import * as beersActions from '../store/beers/actions';
import * as beersSelectors from '../store/beers/reducer';

class App extends Component {
  constructor(props) {
    super(props);
    this.handleSearch = this.handleSearch.bind(this);
  }
  handleSearch(query) {
    this.props.setFilters(query);
    this.props.history.push('/search');
  }
  render() {
    return <AppView handleSearch={this.handleSearch} favoritesCount={this.props.favoritesCount} />;
  }
}

const mapStateToProps = (state) => {
  const [beersArray, beersById, beerIdsArray] = beersSelectors.getBeers(state);
  return {
    beersArray,
    beersById,
    beerIdsArray,
    ratedBeers: beersSelectors.getRatedBeers(state),
    favoritesCount: beersSelectors.getFavoritedBeersCount(state),
    filteredBeers: beersSelectors.getFilteredBeers(state)
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    setFilters: (filters) => dispatch(beersActions.setFilters(filters))
  };
};

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(App)
);
