import React from 'react';
import FeatherIcon from './FeatherIcon';

export default function BeerCard(props) {
  return (
    <div className="card">
      <div className="card__header">
        <input
          type="checkbox"
          id={`item-${props.beer.id}`}
          className="card__input-favorite"
          name="favorites"
          checked={props.beer.isFavorite}
          onChange={() => props.onHeartClick(`${props.beer.id}`)}
        />
        <label htmlFor={`item-${props.beer.id}`} className="card__label-favorite">
          <FeatherIcon name="heart" />
        </label>
      </div>
      <div className="card__body">
        <div className="card__img-wrapper">
          <img className="card__img" src={props.beer.imageURL} alt={props.name} />
        </div>
      </div>
      <div className="card__footer">
        <h4 className="card__title">{props.beer.name}</h4>
        <p className="card__tagline">{props.beer.tagline}</p>
      </div>
    </div>
  );
}
