import React from 'react';
import ReactDOM from 'react-dom';
import { createStore, applyMiddleware, combineReducers } from 'redux';
import { Provider } from 'react-redux';
import thunk from 'redux-thunk';
import * as reducers from './js/store/reducers';
import './css/styles.css';
import App from './js/containers/App';
import LocalStorageService from './js/services/localStorage';
import * as serviceWorker from './serviceWorker';
import { Router } from 'react-router';
import HistoryService from './js/services/history';

const store = createStore(combineReducers(reducers), applyMiddleware(thunk));
const history = HistoryService.history;

window.addEventListener('unload', () => {
  const state = store.getState().beers;
  LocalStorageService.saveFavoritedBeers(state);
});

ReactDOM.render(
  <Provider store={store}>
    <Router basename={process.env.PUBLIC_URL} history={history}>
      <App />
    </Router>
  </Provider>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.register();
